import {postRequest,putRequest,deleteRequest,getRequest} from "../utils/request";

export const listMessage = (params) => {
    return postRequest("/uiapi/v1/message/list", params)
}

export const createMessage = (params) => {
    return postRequest("/uiapi/v1/message/add", params)
}

export const sendMessage = (params) => {
    return postRequest("/uiapi/v1/message/send", params)
}

export const deleteMessage = (params) => {
    return deleteRequest("/uiapi/v1/message/delete", params)
}

export const updateMessage = (params) => {
    return putRequest("/uiapi/v1/message/update", params)
}


export const listMessageByUserId = (params) => {
    return postRequest("/uiapi/v1/message/list/user", params)
}

export const updateMessageStatus = (userId,messageId) => {
    return putRequest("/uiapi/v1/message/update/relation?userId=" + userId + "&messageId=" + messageId, null)
}