<template>
    <div style="margin-top: 40px">
        <el-form :inline="true" :model="queryParam" class="demo-form-inline">
            <el-form-item label="消息头">
                <el-input v-model="queryParam.name" placeholder="消息头" clearable></el-input>
            </el-form-item>
            <el-form-item label="消息状态">
                <el-select v-model="queryParam.conditions.status" placeholder="" clearable @clear="selectClear('status')">
                    <el-option :label="item.name" :value="item.value" v-for="item in messageStatus"></el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="消息等级">
                <el-select v-model="queryParam.conditions.level" placeholder="" clearable @clear="selectClear('level')">
                    <el-option :label="item.name" :value="item.value" v-for="item in messageLevels"></el-option>
                </el-select>
            </el-form-item>
            <el-form-item>
                <el-button type="primary" @click="onSubmit">查询</el-button>
            </el-form-item>
        </el-form>


        <el-table
                :data="listData"
                stripe
                style="width: 100%">
            <el-table-column
                    prop="header"
                    label="消息头"
                    width="430">
            </el-table-column>
            <el-table-column
                    prop="level"
                    label="消息等级"
                    width="300"
                    :formatter="formatLevel"
            >
            </el-table-column>
            <el-table-column
                    prop="readStatus"
                    label="消息状态"
                    :formatter="formatStatus"
            >
            </el-table-column>
            <el-table-column
                    label="操作"  width="180">
                <template slot-scope="scope">
                    <el-button type="text"  @click="detail(scope.row)">详情</el-button>
                </template>

            </el-table-column>
        </el-table>
        <el-row :gutter="20" style="margin-top: 25px">
            <el-col :span="4" :offset="18">
                <el-pagination
                        background
                        :layout=pageInfo.layout
                        :total="pageInfo.total">
                </el-pagination>
            </el-col>
        </el-row>


        <el-dialog
                title="创建消息"
                :visible.sync="dialogVisible"
                width="40%">
            <el-form ref="form"  :model="formData" label-width="200px" size="medium">
                <el-form-item label="消息头" prop="header" >
                    <el-input v-model="formData.header" style="width: 350px" :disabled="isDetail"></el-input>
                </el-form-item>
                <el-form-item label="消息内容" prop="content">
                    <el-input v-model="formData.content" type="textarea"   rows="15" style="width: 350px" :disabled="isDetail"></el-input>
                </el-form-item>
                <el-form-item label="消息等级" prop="level">
                    <el-select v-model="formData.level" placeholder="" style="width: 350px" :disabled="isDetail">
                        <el-option :label="item.name" :value="item.value" v-for="item in messageLevels"></el-option>
                    </el-select>
                </el-form-item>
<!--                <el-form-item v-if="isDetail" label="消息状态" prop="readStatus" :disabled="isDetail">-->
<!--                    <el-select v-model="formData.status" placeholder="" style="width: 350px">-->
<!--                        <el-option :label="item.name" :value="item.value" v-for="item in messageStatus"></el-option>-->
<!--                    </el-select>-->
<!--                </el-form-item>-->
                <el-form-item>
                    <el-button @click="dialogVisible = false">取消</el-button>
                </el-form-item>
            </el-form>
        </el-dialog>

    </div>
</template>

<script>
    import {listMessageByUserId,updateMessageStatus} from "../../../api/messageApi";

    export default {
        name: "userList",
        data() {
            return {
                listData: [],
                queryParam: {
                    "pageNumber": 1,
                    "pageSize": 10,
                    "name": null,
                    "conditions": {
                        "status": null,
                        "level": null,
                        "userId": JSON.parse(window.sessionStorage.getItem('user')).userId
                    }

                },
                dialogVisible: false,
                formData: {
                    id: null,
                    header: null,
                    content: null,
                    level: null,
                    readStatus: null,
                },
                managerList: [
                    {
                        "name": "SmsAdmin",
                        "code": "SmsAdmin"
                    }
                ],
                pageInfo: {
                    layout: "prev, pager, next, jumper, total",
                    total: 10
                },
                messageLevels: [
                    {
                        "name": "一般",
                        "value": 1
                    },
                    {
                        "name": "中等",
                        "value": 2
                    },
                    {
                        "name": "重要",
                        "value": 3
                    }
                ],
                messageStatus: [
                    {
                        "name": "未读",
                        "value": 0
                    },
                    {
                        "name": "已读",
                        "value": 1
                    },
                ],
                isDetail: true
            }
        },
        created() {
            this.onSubmit()
        },
        methods : {
            listMessages() {
                listMessageByUserId(this.queryParam).then(res => {
                    this.listData = res.data.first
                    this.pageInfo.total = res.data.second
                })
            },
            onSubmit() {
                this.listMessages()
            },
            formatLevel(row,column) {
                if (row.level === 1) {
                    return "一般"
                } else if (row.level === 2) {
                    return "中等"
                } else {
                    return "重要"
                }
            },
            formatStatus(row,column) {
                if (row.readStatus === 0) {
                    return "未读"
                } else {
                    return "已读"
                }
            },
            initFormData(row) {
                if (row != null) {
                    this.formData = {
                        id: row.id,
                        header: row.header,
                        content: row.content,
                        level: row.level,
                        readStatus: row.readStatus
                    }
                } else {
                    this.formData = {
                        id: null,
                        header: null,
                        content: null,
                        level: null,
                        readStatus: null
                    }
                }

            },
            detail(row) {
                this.initFormData(row);
                this.dialogVisible = true
                updateMessageStatus(this.queryParam.conditions.userId,row.id).then(res => {
                    this.listMessages()
                })
            },
            selectClear(data) {
                this.queryParam.conditions[data] = null
            }

        }
    }
</script>

<style scoped>

</style>